<template>
    <v-row>
        <v-col>
            <v-card class="mb-4">
                <v-card-text class="d-flex">
                    <v-text-field label="Search" v-model="pageContext.search" hideDetails class="mr-2" @keydown.enter="getData" />
                    <v-btn icon="mdi-filter-multiple" size="small"></v-btn>
                </v-card-text>
            </v-card>

            <data-table :tableData="tableData" @pageChanged="pageChanged">
                <template v-slot:[`item.itsId`]="{ item }">
                    <div class="d-flex flex-column align-center pa-2">
                        <v-img :src="item.raw.pictureInUniform" width="60px" height="60px"
                            style="border:1px Solid #c6c6c6; border-radius: 4px;" cover />
                        {{ item.raw.itsId }}
                    </div>
                </template>
                <template v-slot:[`item.section.description`]="{item}">
                    {{ item.raw.section.description }} ({{ item.raw.organization.name }})
                </template>
                <template v-slot:[`item.Actions`]="{ item }">
                    <v-btn icon="mdi-magnify" size="small" @click="viewMember(item.raw)" />
                </template>
            </data-table>
            <!-- <v-card>
                <v-card-text>
                </v-card-text>
            </v-card> -->
        </v-col>
    </v-row>
</template>

<script setup>

import { reactive, onMounted, inject } from 'vue';
import { getAll } from '@/services/memberService';
import { useRouter } from "vue-router";

const loader = inject("loader");
const router = useRouter();

const pageContext = reactive({
    pageNumber: 1,
    pageSize: 20,
    totalItems: 0,
    totalPages: 0,
    hasNextPage: false,
    hasPreviousPage: false,
    pageIndex: 0,
    search: "",
    itsId: 0,
    organizationId: 0
});

const tableData = reactive({
    headers: [
        { title: "S. No", key: "SNo" },
        { title: "Its", key: "itsId" },
        { title: "Name", key: "fullName" },
        { title: "Rank", key: "rank.description" },
        { title: "Section", key: "section.description" },
        { title: "Mobile", key: "mobileNo" },
        { title: "Actions", key: "Actions" },
    ],
    data: [],
    itemsPerPage: 15,
    totalItems: 0,
    paginationContext: {},
});

async function getData() {
    loader.show();

    let result = await getAll(pageContext)
    .catch(error => console.log(error))
    .finally(() => { 
        loader.hide();
    });

    console.log(result);

    tableData.data = result.members;
    tableData.paginationContext = result.listContext;
}

function viewMember(member) {
    router.push(`/members/${member.itsId}`);
}

async function pageChanged(pageNo) {
    pageContext.pageNumber = pageNo;
    await getData();
}

onMounted(async () => {
    await getData();
});

</script>