<template>
    <v-row v-if="'sharafTitle' in sharaf">
        <v-col cols="12">
            <v-card>
                <v-card-text>
                    <h6 class="text-h6 font-weight-bold">
                        {{ sharaf.sharafTitle }}<br />
                        <small><span class="text-red">Live from: {{ moment(sharaf.startDate).format("D-MMM-yyyy") }} to {{
                            moment(sharaf.endDate).format("D-MMM-yyyy") }}</span></small>
                    </h6>
                    <h6 class="text-subtitle-1" v-html="sharaf.description"></h6>
                </v-card-text>
            </v-card>
        </v-col>
        <v-col cols="12">
            <v-card>
                <v-card-text class="d-flex">
                    <v-autocomplete label="Member" v-model="selectedMemberId" :items="members" item-title="fullName"
                        item-value="itsId" :custom-filter="customFilter" class="mr-2" hide-details>
                        <template v-slot:item="{ props, item }">
                            <v-list-item v-bind="props" :prepend-avatar="item?.raw?.pictureInIts"
                                :title="item?.raw?.fullName" :subtitle="item?.raw?.itsId"></v-list-item>
                        </template>
                    </v-autocomplete>
                    <v-btn prepend-icon="mdi-plus-circle" @click="addMember">Add</v-btn>
                </v-card-text>
                <v-divider />
                <v-card-text>
                    <v-list density="compact">
                        <v-list-item v-for="member in registrations" :key="member.itsId"
                            :title="`${member.member.itsId} - ${member.member.fullName}`"
                            :subtitle="`${member.member.rank.description} - ${member.member.organization.name}`"
                            density="compact">
                            <template v-slot:append>
                                <v-btn icon="mdi-delete" size="small" color="error" @click="deleteMember(member.member.itsId)" />
                            </template>
                        </v-list-item>
                    </v-list>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>

<script setup>
import { ref, inject, onMounted } from 'vue';
import { useRoute } from "vue-router";
import { getById, getEligibleMembers, getRegistrations, addRegistration, deleteRegistration } from '@/services/sharafService';
import moment from 'moment';

const loader = inject("loader");
const snackbar = inject("snackBar");
const route = useRoute();

const sharafId = route.params.id;
const sharaf = ref({});
const members = ref([]);
const registrations = ref([]);
const selectedMemberId = ref(null);

function customFilter(itemTitle, queryText, item) {
    const fullName = item.raw.fullName.toLowerCase();
    const itsId = item.raw.itsId.toString();
    const searchText = queryText.toLowerCase();

    console.log(itemTitle);
    return fullName.indexOf(searchText) > -1 || itsId.indexOf(searchText);
}

async function getData() {
    loader.show();

    let result = await getById(sharafId)
        .catch(error => console.log(error))
        .finally(async () => {
            await getMembers();
            await getDetail();
            loader.hide();
        });

    sharaf.value = result;
}

async function getMembers() {
    let result = await getEligibleMembers(sharafId)
        .catch(error => console.log(error))

    members.value = result;
}

async function getDetail() {
    let result = await getRegistrations(sharafId)
        .catch(error => console.log(error))

    registrations.value = result;
}

async function addMember() {
    if (selectedMemberId.value == null) {
        snackbar.error("Please select a member");
        return;
    }

    loader.show();

    await addRegistration(sharafId, selectedMemberId.value)
        .catch(error => {
            snackbar.error(error);
            console.log(error);
        })
        .finally(async () => {
            await getDetail();
            loader.hide();
        });

    selectedMemberId.value = null;
}

async function deleteMember(itsId) {
    loader.show();

    await deleteRegistration(sharafId, itsId)
        .finally(async () => {
            await getDetail();
            loader.hide();
        });
}

onMounted(async () => {
    await getData();
});

</script>