<template>
    <v-row v-if="'description' in  event">
        <v-col>
            <v-card>
                <v-card-text>
                    <h6 class="text-h6 font-weight-bold">
                        {{ event.description }} - {{ moment(event.dutyDate).format("D-MMM-yyyy") }}<br />
                        <small><span class="text-red">
                                Reporting Time: {{ moment(event.reportingTime).format("hh:mm A") }}
                            </span></small><br />
                        <small><span class="text-red">Scanning from: {{ moment(event.scanningStart).format("hh:mm A") }} to
                                {{
                                    moment(event.scanningEnd).format("hh:mm A") }}</span></small>
                    </h6>
                </v-card-text>
            </v-card>
            <v-card class="mt-3">
                <v-card-text>
                    <v-list density="compact" v-if="attendance.length > 0">
                        <v-list-item density="compact" v-for="att in attendance" :key="att.member.itsId"
                            :title="`${att.member.itsId} - ${att.member.fullName} - ${att.member.rank.description} (${att.member.organization.name})`" :subtitle="formatTime(att.reportingTime)" :prepend-avatar="att.member.pictureInUniform">
                        </v-list-item>
                    </v-list>
                    <h4 v-else class="text-center">No Attendance Marked</h4>
                </v-card-text>
            </v-card>
            <v-card class="mt-3">
                <v-card-text>
                    <v-text-field v-model="selectedMemberId" label="Enter Its Number" @keydown.enter="addMember" hide-details />
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>
<script setup>
import { ref, inject, onMounted } from 'vue';
import { useRoute } from "vue-router";
import { getById, getAttendance, addAttendance } from '@/services/attendanceService';
import moment from 'moment';

const loader = inject("loader");
const snackbar = inject("snackBar");
const route = useRoute();

const attendanceId = route.params.id;
const event = ref({});
const attendance = ref([]);
const selectedMemberId = ref("");

function formatTime(dt) {
    return moment(dt).format("hh:mm A");
}

async function getData() {
    loader.show();

    let result = await getById(attendanceId)
        .catch(error => console.log(error))
        .finally(async () => {
            await getDetail();
            loader.hide();
        });

    event.value = result;
}

async function getDetail() {
    let result = await getAttendance(attendanceId)
        .catch(error => console.log(error))

    attendance.value = result;
}

async function addMember() {
    if (selectedMemberId.value == null) {
        snackbar.error("Please enter an Its number");
        return;
    }

    loader.show();

    await addAttendance(attendanceId, selectedMemberId.value)
        .then(() => snackbar.success("Attendance has been marked successfully"))
        .catch(error => {
            snackbar.error(error);
            console.log(error);
        })
        .finally(async () => {
            await getDetail();
            loader.hide();
        });

    selectedMemberId.value = null;
}

onMounted(async () => {
    await getData();
});

</script>